var lastPage;

class PageHandler {
  constructor(initPage){
    this.page = initPage;
    lastPage = this.page;

    this.home = <Home />;
    this.contact = <Contact />;
  }

  getPage(){ return this.page; }
  setPage(page){
    this.page = page;
  }
  gotoHome(){this.setPage(this.home);}
  gotoContact(){this.setPage(this.contact);}
}

export const pageHandler = new PageHandler(<Home />);

const App = () => {
  const [update, setUpdate] = useState(false);
  const [fun, setFun] = useState(false);

  function checkUpdate(){
    if(pageHandler.getPage() != lastPage){
      lastPage = pageHandler.getPage();
      setUpdate(true);
    }
  }

  function switchFun(){
    let button = document.getElementById("fun");

    if(fun){
      button.innerText = "Feeling Lucky?";
      let contentContainers = document.getElementsByClassName("content");
      contentContainers[0].style["display"] = "block";

      gameHandler.signalStop();
      let gameContainers = document.getElementsByClassName("game-content");
      gameContainers[0].style["display"] = "none";
    } else {
      button.innerText = "Back To Resume";
      let contentContainers = document.getElementsByClassName("content");
      contentContainers[0].style["display"] = "none";

      let gameContainers = document.getElementsByClassName("game-content");
      gameContainers[0].style["display"] = "block";
    }
    setFun(!fun);
  }

  useEffect(() => {
    setInterval(checkUpdate, 100);

    if(update){
      setUpdate(false);
    }
  });

  return(
    <>
      <button type="button" id="fun" onClick={switchFun}>Feeling Lucky?</button>
      <div className="content" style={{"display": "block"}}>
        <Home />
        <Contact />
      </div>
      <div className="game-content" style={{"display": "none"}}>
        <MinigameHandler />
      </div>
    </>
  );
}

export default App;
