import React from 'react';
import './Contact.css';
import { sendMessage } from '../../services/MessageService.js';
import { useState } from 'react';

// Icons
import Twitter from '../../assets/twitter.svg';
import Github from '../../assets/github.svg';

var canSubmit = true;

const Contact = () => {
  const revertTimeout = () => {
    canSubmit = !canSubmit;
  }

  const pushMessage = (e) => {
    let name = document.getElementById("name");
    let email = document.getElementById("email");
    let content = document.getElementById("content");

    if (!canSubmit) {
      alert("You must wait before submitting another message.");
      return;
    }

    if(name.value == "" ||
      email.value == "" ||
      content.value == ""){
        alert("Please fill in the form correctly before submitting.");
        return;
    }

    sendMessage(
      {
        "name": name.value,
        "email": email.value,
        "content": content.value
      }
    ).then(() => {
      name.value = "";
      email.value = "";
      content.value = "";
      revertTimeout();
    }).then(() => {
      alert("Your message has been stored, please allow time for me to respond before following up!");
    }).then(() => {
      setTimeout(revertTimeout, 30*1000);
    });
  }

  return(
    <>
      <div className="contactContainer">
        <div className="socials">
          <a href="https://twitter.com/probablynotwizz"><img src={Twitter} alt="Twitter" className="social" /></a>
          <a href="https://github.com/probablynotkai/"><img src={Github} alt="Github" className="social" /></a>
        </div>

        <div className="formContainer">
          <h1 style={{"textAlign": "center"}}>Looking for something else?</h1>

          <form className="contactForm">
            <input type="text" id="name" placeholder="Name" />
            <input type="text" id="email" placeholder="Email" />
            <textarea type="text" id="content" className="long-text" placeholder="Write here..."></textarea>

            <button type="button" id="submit" onClick={(e) => pushMessage(e)}>Send message</button>
          </form>
        </div>
      </div>
    </>
  );
}

export default Contact;
