import React from 'react';
import Player from './player/Player';
import Apple from './apple/Apple';
import { useState, useEffect } from 'react';
import './MinigameHandler.css';

/*
Apple:
Start : 0,0
(Random 1-14) * 20 = Position
Check snake top and left with apple top and left for if eaten.
useState - currentApple, setCurrentApple
useEffect - if currentApple === Undefined, populate state
*/

/*
TODO:
Advance the gameplay
*/

class GameHandler {
	constructor() {
		this.apple = <Apple />;
		this.score = 0;
		this.stopped = false;
	}

	signalStop() { this.stopped = true; }
	getScore() { return this.score; }
	setScore(score) { this.score = score; }
	setApple(apple) { this.apple = apple; }
	getApple() { return this.apple; }
	regenApple() {
		regenerate();
		this.score = this.score + 1;
	}
}

const gameHandler = new GameHandler();

function regenerate() {
	let randomX = generateRandom();
	let randomY = generateRandom();

	let myApple = document.getElementsByClassName("apple")[0];
	myApple.style["display"] = "block";
	myApple.style["top"] = randomX + "px";
	myApple.style["left"] = randomY + "px";
}

function generateRandom() {
	let random = parseInt(((Math.random() * 13) + 1) * 2) * 10;
	return (random % 20 === 0 ? random : random + 10);
}

const MinigameHandler = () => {
	const [started, setStarted] = useState(false);
	const [ended, setEnded] = useState(false);
	var updateScoreInterval;
	var checkStopInterval;

	function updateScore() {
		let scoreboard = document.getElementById("score");
		scoreboard.innerHTML = gameHandler.getScore();
		scoreboard.style["display"] = "block";
	}

	function startGame() {
		setStarted(true);
		setTimeout(regenerate, 250);
		updateScoreInterval = setInterval(updateScore, 50);
		checkStopInterval = setInterval(checkStop, 250);
	}

	function restartGame() {
		setStarted(false);
		setEnded(false);
		gameHandler.setScore(0);
		gameHandler.stopped = false;
		updateScore();
		startGame();
	}

	function goToMenu(){
		setStarted(false);
		setEnded(false);
		gameHandler.setScore(0);
		gameHandler.stopped = false;
	}

	function checkStop() {
		if (gameHandler.stopped) {
			clearInterval(updateScoreInterval);
			clearInterval(checkStopInterval);

			let scoreboard = document.getElementById("score");
			scoreboard.style["display"] = "none";

			setEnded(true);
		}
	}

	return (
		<>
			<div id="game-container">
        <h1 id="score" style={{ "display": "none" }}>0</h1>
        {
          started
            ?
            (
              ended
                ?
                <div className="gameOver">
                  <h1>Game Over</h1>
                  <h2>You scored {gameHandler.getScore()} points.</h2>
                  <button type="button" onClick={restartGame}>Restart Game</button> <br />
                  <button type="button" onClick={goToMenu}>Main Menu</button>
                </div>
                :
                <div className="minigame-container">
                  {gameHandler.getApple()}
                  <Player />
                </div>
            )
            :
            <div className="menu">
              <h2>Block Game</h2>
              <p>Use <span style={{"color": "#588157"}}>WASD</span> or <span style={{"color": "#588157"}}>Arrow Keys</span> to navigate.</p>
              <button id="start" onClick={startGame}>Start Game</button>
            </div>
        }
      </div>
		</>
	);
}

export default MinigameHandler;
export { gameHandler };
