import React from 'react';
import './Player.css';
import { useEffect } from 'react';
import { gameHandler } from '../MinigameHandler.js';

var nextKey = "s";
var lastKey;
var deathChance = false;
var continuousMovementInterval;

// Must be on a tick, 750?
function checkDeath() {
	let player = document.getElementsByClassName("player")[0];
	let top = player.style["top"].substring(0, player.style["top"].length - 2);
	let left = player.style["left"].substring(0, player.style["left"].length - 2);

	if ((top == -20 && (nextKey == "w" || nextKey == "ArrowUp"))
		|| (top == 260 && (nextKey == "s" || nextKey == "ArrowDown"))
		|| (left == 0 && (nextKey == "a" || nextKey == "ArrowLeft"))
		|| (left == 280 && (nextKey == "d" || nextKey == "ArrowRight"))) {
		if(!deathChance){
			deathChance = !deathChance;
		} else {
			clearInterval(continuousMovementInterval);
			gameHandler.signalStop();
			deathChance = false;
		}
	} else{
		if(deathChance){
			deathChance = !deathChance;
		}
	}
}

function isValidInput(event){
	return (
		event.key == "w"
		|| event.key == "a"
		|| event.key == "s"
		|| event.key == "d"
		|| event.key == "ArrowUp"
		|| event.key == "ArrowDown"
		|| event.key == "ArrowLeft"
		|| event.key == "ArrowRight"
	);
}

function continuousMovement() {
	let player = document.getElementsByClassName("player")[0];

	if(player == undefined) return;

	let top = player.style["top"].substring(0, player.style["top"].length - 2);
	let left = player.style["left"].substring(0, player.style["left"].length - 2);

	switch (nextKey) {
		case "w":
		case "ArrowUp":
			if (parseInt(top) > -20) {
				player.style["top"] = (parseInt(top) - 20) + "px";
			}
			break;
		case "s":
		case "ArrowDown":
			if (parseInt(top) < 260) {
				player.style["top"] = (parseInt(top) + 20) + "px";
			}
			break;
		case "a":
		case "ArrowLeft":
			if (parseInt(left) > 0) {
				player.style["left"] = (parseInt(left) - 20) + "px";
			}
			break;
		case "d":
		case "ArrowRight":
			if (parseInt(left) < 280) {
				player.style["left"] = (parseInt(left) + 20) + "px";
			}
			break;
	}
	checkDeath();
}

function handleEvent(event) {
	lastKey = nextKey;
	nextKey = (
		((lastKey == "w" || lastKey == "ArrowUp") && (event.key != "s" || event.key != "ArrowDown"))
		|| ((lastKey == "s" || lastKey == "ArrowDown") && (event.key != "w" || event.key != "ArrowUp"))
		|| ((lastKey == "a" || lastKey == "ArrowLeft") && (event.key != "d" || event.key != "ArrowRight"))
		|| ((lastKey == "d" || lastKey == "ArrowRight") && (event.key != "a" || event.key != "ArrowLeft"))
		? (isValidInput(event) ? event.key : lastKey)
		: lastKey
	);
}

const Player = () => {
	function initContinuousMovement(){
		continuousMovementInterval = setInterval(continuousMovement, 80);
	}

	useEffect(() => {
		if(continuousMovementInterval != undefined){
			clearInterval(continuousMovementInterval);
		}

		let player = document.getElementsByClassName("snake")[0];
		lastKey = "";
		nextKey = "s";

		document.addEventListener(
			"keydown", (event) => {
				handleEvent(event);
			}
		);

		setTimeout(initContinuousMovement, 500);
	}, []);

	return (
		<>
			<div className="player" style={{ "top": "0", "left": "20px" }}>
			</div>
		</>
	);
}

export default Player;
