import React from 'react';
import { useEffect } from 'react';
import './Apple.css';
import { gameHandler } from '../MinigameHandler.js';

/*
Apple:
Start : 0,0
(Random 1-14) * 20 = Position
Check snake top and left with apple top and left for if eaten.
useState - currentApple, setCurrentApple
useEffect - if currentApple === Undefined, populate state
*/
var eatenCheckInterval;

function eatenCheck() {
	if (gameHandler.stopped) {
		clearInterval(eatenCheckInterval);
		return;
	}

	let snake = document.getElementsByClassName("player")[0];

	let snakeY = snake.style["top"].substring(0, snake.style["top"].length - 2);
	let snakeX = snake.style["left"].substring(0, snake.style["left"].length - 2);

	let myApple = document.getElementsByClassName("apple")[0];
	let appleY = myApple.style["top"].substring(0, myApple.style["top"].length - 2) - 20;
	let appleX = myApple.style["left"].substring(0, myApple.style["left"].length - 2);

	if (snakeX == appleX && snakeY == appleY) {
		gameHandler.regenApple();
	}
}

const Apple = () => {
	useEffect(() => {
		if(eatenCheckInterval != undefined){
			clearInterval(eatenCheckInterval);
		}

		eatenCheckInterval = setInterval(eatenCheck, 50);
	}, []);

	return (
		<>
			<div className="apple" style={{ "top": "0px", "display": "none" }}>
			</div>
		</>
	);
}

export default Apple;
